@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;0,400;1,300&display=swap);
nav{display:flex;justify-content:space-between;align-items:center;background-color:rgba(0,0,0,0.5);padding:1em 4em;color:#efefef}nav .logo{font-size:1.4em;font-family:600;letter-spacing:0.1em}nav .user-links{display:flex;grid-gap:1.5em;gap:1.5em}

.toast{background:#fff;transition:0.3s ease;position:relative;pointer-events:auto;overflow:hidden;margin:0 0 6px;padding:30px;margin-bottom:15px;width:300px;max-height:100px;border-radius:3px 3px 3px 3px;box-shadow:0 0 10px #999;opacity:0.9;-webkit-animation:toast-in-right 0.7s;animation:toast-in-right 0.7s}.toast.error{background-color:#ff5959}.toast.success{background-color:#2cb978}.toast:hover{box-shadow:0 0 12px #fff;opacity:1;cursor:pointer}.toast .toast-title{font-weight:700;font-size:16px;text-align:left;margin-top:0;margin-bottom:6px;width:300px;height:18px}.toast p{margin:0;text-align:left;height:18px;margin-left:-1px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.toast.expiration-animate{-webkit-animation:expires 0.8s forwards ease;animation:expires 0.8s forwards ease}@-webkit-keyframes toast-in-right{from{-webkit-transform:translateX(100%);transform:translateX(100%)}to{-webkit-transform:translateX(0);transform:translateX(0)}}@keyframes toast-in-right{from{-webkit-transform:translateX(100%);transform:translateX(100%)}to{-webkit-transform:translateX(0);transform:translateX(0)}}@-webkit-keyframes expires{from{opacity:1}to{opacity:0;display:none}}@keyframes expires{from{opacity:1}to{opacity:0;display:none}}

.toast-container{font-size:14px;box-sizing:border-box;position:fixed;z-index:999999;right:12px;bottom:12px;transition:-webkit-transform 0.6s ease-in-out;transition:transform 0.6s ease-in-out;transition:transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;-webkit-animation:toast-in-right 0.7s;animation:toast-in-right 0.7s}

.form-container{padding:2rem 2rem;width:30%;background:radial-gradient(ellipse at left bottom, #16182f 0%, rgba(38,20,72,0.9) 59%, rgba(17,27,75,0.9) 100%);box-shadow:0 50px 70px -20px rgba(0,0,0,0.8);margin:0 auto;border:1px solid;border-color:#333333;margin-top:2rem;padding:5rem;color:var(--text-color)}form{display:flex;flex-direction:column;justify-content:space-around;align-items:center;grid-gap:1.5rem;gap:1.5rem}form label{color:white;margin:14px 0;display:block;font-size:1em;line-height:1}form input{color:#efefef;text-indent:0.5rem;font-size:1em;width:100%;padding:0.5rem 0.5rem;border:2px solid;border-color:rgba(255,255,255,0.3);background:rgba(255,255,255,0.1)}form input:focus{outline:none;border-color:rgba(2,1,1,0.1)}form button{border:none;outline:none;width:100%;padding:15px 0;color:#fff;font-size:16px;letter-spacing:1px;background:#603bbb;cursor:pointer;transition:0.4s ease-in-out}form button:hover{background:#4d2e9c;transition:0.4s ease-in-out}form button:disabled{background:#8d8b94}form a.forgot-password{font-size:1em;align-self:flex-start;color:#efefef}form a.forgot-password:hover{text-decoration:underline}form a{color:#f3f169}form a:hover{color:#e4e133;text-decoration:underline}@media (max-width: 960px){.form-container{width:80%}}

input{color:#efefef;text-indent:0.5rem;font-size:1em;width:100%;padding:0.5rem 0.5rem;border:2px solid;border-color:rgba(255,255,255,0.3);background:rgba(255,255,255,0.1)}input:focus{outline:none;border-color:rgba(2,1,1,0.1)}.error-message{color:#ff5959;font-size:1em}.field-container{width:100%}

.profile-conteiner{margin-top:4rem;background:#7a7676;width:70%;display:flex;margin:0 auto;grid-gap:6rem;gap:6rem;padding:1rem 3rem}.profile-conteiner .user-avatar{display:flex;flex-direction:column;background:#efefef}.profile-conteiner .user-avatar img{object-fit:cover}.profile-conteiner .user-info{width:100%;display:flex;flex-direction:column;align-items:center}

p{color:#efefef}a{color:"#393bcf";-webkit-text-decoration:"underline";text-decoration:"underline"}

.main-conteiner{display:flex;width:80%;border:1px solid #efefef;padding:2rem 2rem;margin:0 auto;max-height:500px;overflow-y:scroll}.main-conteiner .chat-container{padding-right:1em;display:flex;flex-direction:column;width:80%}.main-conteiner .user-list{padding:0.6em 0.6em;color:#efefef;display:flex;flex-direction:column;grid-gap:0.5em;gap:0.5em;width:10%;border-left:1px solid #efefef}

.message-body{display:flex;padding:0.5rem 0.5rem;flex-direction:column;background:rgba(255,255,255,0.1);margin-bottom:1rem}

.rooms-list{display:flex;flex-direction:column;grid-gap:1rem;gap:1rem;color:#efefef}.rooms-list a{padding:0.5em}.rooms-list a.active{background:#706f6f}

.users-container{display:flex;grid-gap:1rem;gap:1rem;flex-direction:column;color:#efefef}.users-container a,.users-container p{padding:0.5em}.users-container a.active,.users-container p.active{background:#706f6f}.users-container .new-messages-icon{font-style:normal;padding:0.2em 0.4em;border-radius:50%;background-color:#b35050}

.chat-page{margin-top:3rem;display:flex}

:root{--primary-color: #278ea5;--secondary-color: #8ecad1;--text-color: #efefef;--text-color-muted: #c0bdbd}html,body{background:#333;padding:0;margin:0;font-family:"Montserrat", sans-serif}a{color:inherit;text-decoration:none;cursor:pointer}*{box-sizing:border-box}

